.country-filter {
  background-color: @pale-grey-four;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  position: relative;
  padding-top: 20px;
  margin-top: -20px;
}

.country-filter__wrapper {
  width: 100vw;
  display: block;
  position: absolute;
  z-index: 4;
  background-color: @pale-grey-four;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  min-height: 730px;
  padding-top: 20px;
  margin-top: -20px;

  &--hidden {
    display: none;
  }
}

.country-filter__country-wrapper {
  margin: 0 auto;
  max-width: 270px;
}

.country-filter__title-wrapper {
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 270px;
}

.country-filter__mainland-wrapper {

  .country-filter__open-btn {
    display: none;
  }
}

.country-filter__list {
  position: relative;
  margin-left: 25px;
  margin-top: 17px;
}

.country-filter__open-btn {
  background-color: transparent;
  margin-left: 21px;
  width: 17px;
  height: 17px;
  font-size: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.country-filter__btn-text {
  display: none;
}

.country-filter__icon {
  fill: @dark-blue-grey;
  width: 17px;
  height: 3px;
}

.country-filter__title {
  color: @dark-blue;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.38;
  text-transform: uppercase;
  padding-left: 27px;
  position: relative;
  margin-top: 15px;

  &::before {
    position: absolute;
    top: 5px;
    left: 0;
    content: "";
    width: 15px;
    height: 10px;
    background: url("../img/filter.svg") no-repeat;
  }
}

.country-filter__btn {
  width: 270px;
  height: 40px;
  border-radius: 20px;
  background-color: @light-periwinkle;
  position: absolute;
  left: 50%;
  bottom: 33px;
  transform: translateX(-50%);
  font-size: 16.5px;
  font-weight: bold;
  text-transform: uppercase;
  line-height: 1.79;
  padding-left: 36px;
  cursor: pointer;

  &::before {
    position: absolute;
    bottom: 15px;
    left: 83px;
    content: "";
    width: 12px;
    height: 12px;
    background: url("../img/filter-close.svg") no-repeat;
  }

  &:hover {
    background-color: @light-blue-grey-four;
  }

  &:active {
    color: @dark-slate-blue-30;

    &::before {
      opacity: 0.3;
    }
  }
}

@media (min-width: @tablet-width) {
  .country-filter {
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
    min-height: 120px;
    padding-top: 50px;
    margin-top: -50px;
  }

  .country-filter__wrapper {
    min-width: @tablet-width;
    min-height: 800px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding: 0 60px;
    border-bottom-left-radius: 60px;
    border-bottom-right-radius: 60px;
  }

  .country-filter__mainland-wrapper {
    position: absolute;

    .country-filter__open-btn {
      display: block;
    }
  }

  .country-filter__country-wrapper {
    max-width: 618px;
    margin: 0 auto;
    margin-right: auto;
  }

  .country-filter__title-wrapper {
    justify-content: flex-start;
  }

  .country-filter__title {
    display: none;
  }

  .country-filter__open-btn {
    display: flex;
    align-items: center;
    width: 220px;
    height: auto;
    font-size: 20px;
    text-transform: uppercase;
    color: @dark-blue-grey;
    line-height: 1.2;
    font-weight: bold;
    position: absolute;
    top: 57px;
    left: 490px;
  }

  .country-filter__btn-text {
    display: block;
  }

  .country-filter__list {
    padding: 0;
    margin-left: 15px;
    margin-top: 54px;
    margin-bottom: 65px;
  }

  .country-filter__icon {
    width: 27px;
    height: 5px;
    margin-left: 20px;
  }

  .country-filter__btn {
    width: 618px;
    height: 100px;

    &::before {
      width: 19px;
      height: 19px;
      top: 40px;
      left: 240px;
    }
  }
}

@media (min-width: @desktop-width) {
  .country-filter__wrapper {
    max-width: @desktop-width;
    position: relative;
    padding: 0 10px;
  }

  .country-filter__title {
    display: block;
    position: absolute;
    z-index: 10;
    left: 95px;
    top: 75px;
    font-size: 20px;

    &::before {
      width: 23px;
      height: 15px;
      top: 3px;
      left: -18px;
    }
  }

  .country-filter__country-wrapper {
    max-width: 1320px;
    margin: 0 auto;
  }

  .country-filter__list {
    margin-left: 342px;
  }

  .country-filter__open-btn {
    left: 1150px;
  }

  .country-filter__btn {
    width: 1320px;
    height: 100px;
    font-size: 20px;

    &::before {
      left: 580px;
    }
  }
}
